// STYLES FOR RESULTS PAGES
.page-results-participants,
.page-results-post,
.page-results-video,
.page-results-photo {
    .page-contenet {
        padding-bottom: 216px;
        @media #{$laptop} {
            padding-bottom: 154px;
        }
        @media #{$tablet} {
            padding-bottom: 143px;
        }
        @media #{$mobile} {
            padding-bottom: 88px;
        }
    }
    .content-title {
        padding-bottom: 28px;
        @media #{$laptop} {
            padding-bottom: 41px;
        }
        @media #{$tablet} {
            padding-bottom: 60px;
        }
        @media #{$mobile}  {
            padding-bottom: 50px;
        }
    }
    .exhibitors {
        @media #{$mobile} {
           margin-top: 10px;
        }
    }
    .exhibitors__item-wrap {
        @media #{$laptop} {
            margin-bottom: 32px;
        }
        @media #{$tablet} {
            width: 25%;
            margin-bottom: 24px;
        }
        @media #{$mobile} {
            width: 50%;
            margin-bottom: 19px;        
        }
    }
    .exhibitors-filter {
        @media #{$laptop} {
            padding: 63px 0 0;
        }
        @media #{$tablet} {
            padding: 57px 0 0;
        }
        @media #{$mobile} {
            padding: 37px 0 0;
        }
        &__btn-container {
            @media #{$tablet} {
                width: 25%;
            }
            @media #{$mobile} {
                width: 50%;
                &:nth-child(odd) a {
                    float: right;
                }
            }
        }
        &__big-link {
            @media #{$tablet} {
                height: 125px;
            }
        }
        &__pic-wrap {
            @media #{$tablet} {
                padding: 12px 0 13px;
            }
            @media #{$mobile} {
                padding: 10px 0 11px;
            }
        }
        &__pic-wrap img {
            @media #{$tablet} {
                height: 56px;
            }
            @media #{$mobile} {
                height: 46px;
            }
        }
        &__title {
            @media #{$tablet} {
                font-size: 11px;
                letter-spacing: 1px;
            }
            @media #{$mobile} {
                font-size: 9px;
            }
        }
        &__big-link {
            @media #{$tablet} {
                display: block;
            }
            @media #{$mobile} {
                width: 131px;
                height: 103px;
                box-shadow: 0 0 46px rgba(220, 220, 220, 0.35);
                background-color: #121212;
                margin-bottom: 32px;
            }
        }

    }
    .exhibitors__item {
        @media #{$tablet} {
            height: 96px;
        }
        @media #{$mobile} {
            height: 80px;
        }
    }
    .exhibitors-filter__standart-link {
        @media #{$tablet} {
            display: none;
        }
    }
}

// STYLES SLIDER WITH DATE OF THE RELEASES
.result-calendar {
    &__container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        @media #{$laptop} {
            padding: 0 50px;
        }
         @media #{$tablet} {
            padding: 0 93px;
        }
         @media #{$mobile} {
            max-width: 290px;
            padding: 0;
            margin: 0 auto;
        }
    }
    &__date {
        color: $base-font-color;
        font-size: 18px;
        font-weight: 500;
        text-transform: uppercase;
        text-decoration: none;
        transition: all .3s;
        text-align: center;
        &:hover,
        &:active,
        &:focus,
        &:visited {
            text-decoration: none;
            color: $base-font-color;
        }
        &:hover {
            @media #{$tablet-plus}  {
                color: $accent-color;
            }
        }
        @media #{$laptop} {
            font-size: 16px;
        }
        @media #{$tablet} {
            font-size: 14px;
        }
    }
    &__date.active {
        color: $accent-color;
        font-size: 30px;
        font-weight: 700;
        cursor: default;
        @media #{$laptop} {
            font-size: 25px;
        }
        @media #{$tablet} {
            font-size: 20px;
        }
    }
    &__slider {
        width: 100%;
        overflow: hidden;
        position: relative;
        height: 60px;
    }
    &__body{
        position: absolute;
        display: flex;
        align-items: center;
        width: 9999px;
        height: 60px;
    }
}

// STYLES FOR SECTION WITH SOME BUTTON IN LINE
.selection-buttons {
    margin: 76px 0 112px;
    @media #{$laptop} {
        margin: 52px 0 93px;
    }
    @media #{$tablet} {
        margin: 45px 0 72px;
    }
    @media #{$mobile} {
        margin: 61px 0 0;
    }
    & .button {
        width: 100%;
        min-width: 100%;
        box-shadow: inset 0px 0px 0px 2px $action-color;
        height: 44px;
        &::after {
            font-size: 14px;
            font-weight: 400;
            letter-spacing: 0.7px;
            text-transform: none;
            line-height: 44px;
            padding: 0 5px;
        }
        &:hover::after {
            @media #{$tablet-plus} {
                font-weight: 700;
            }
        }
        @media #{$mobile} {
            max-width: 290px;
            min-width: auto;
            border-radius: 999px;
        }
    }
    & .button.active {
        cursor: default;
        &::before {
            background-image: linear-gradient(to left, #70992a 0%, #abe647 100%);
            opacity: 1;
            left: 0;
        }
        &::after {
            color: $subtitle-font;
            font-weight: 700;
        }
    }
    &__btn-container {
        @media #{$tablet} {
            margin-bottom: 23px;
        }
        @media #{$mobile} {
            text-align: center;
            margin-bottom: 25px;
        }
    }
    &__btn-wrap {
        @media #{$tablet} {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
        }
    }
}

// STYLES FOR RESAULTS POST PAGE
.page-results-post {
    .page-contenet {
        padding-bottom: 125px;
        @media #{$laptop} {
            padding-bottom: 147px;
        }
        @media #{$tablet} {
            padding-bottom: 98px;
        }
        @media #{$mobile} {
            padding-bottom: 120px;
        }
    }
    .selection-buttons {
        margin-bottom: 61px;
        @media #{$laptop} {
            margin-bottom: 51px;
        }
        @media #{$tablet} {
           margin-bottom: 0;
        }
        @media #{$mobile} {
           margin-bottom: 14px;
        }
    }
    .not-numbered-list {
        margin-top: -9px;
        @media #{$laptop} {
            margin-top: 0;
        }
        @media #{$mobile} {
            margin-top: 11px;
        }
    }
}
.results-post {
    color: $base-font-color;
    &__titel,
    h2, h3, h4 {
        font-size: 24px;
        font-weight: 700;
        text-align: center;
        padding: 39px 30px 30px;
        @media #{$laptop} {
            font-size: 21px;
        }
        @media #{$tablet} {
            font-size: 20px;
            padding: 42px 70px 30px;
        }
        @media #{$mobile} {
            font-size: 20px;
            padding: 42px 0 12px;
        }
    }
    &__text {
        font-size: 18px;
        line-height: 24px;
        padding-bottom: 24px;
         @media #{$laptop} {
            font-size: 16px;
        }
         @media #{$tablet} {
            font-size: 14px;
            line-height: 21px;
            padding-bottom: 23px;
        }
    }

}

// STYLES FOR RESAULTS VIDEO PAGE
.page-results-video {
     .page-contenet {
         padding-bottom: 45px;
         @media #{$laptop} {
             padding-bottom: 82px;
         }
         @media #{$tablet} {
             padding-bottom: 74px;
         }
         @media #{$mobile} {
             padding-bottom: 60px;
         }
     }
     .selection-buttons {
        @media #{$tablet} {
            margin-bottom: 68px;
        }    
    }
}
.video-list {
    &__items {
        padding: 14px 0 0 0;
        display: flex;
        flex-wrap: wrap;
        &::before,
        &::after {
            content: none;
        }
        @media #{$laptop} {
            padding-top: 10px;
        }
        @media #{$tablet} {
            padding-top: 0;
        }
        @media #{$mobile} {
            padding-top: 9px;
        }
    }
    &__tiem {
        margin-bottom: 96px;
        @media #{$laptop} {
            margin-bottom: 74px;
        }
        @media #{$tablet} {
            margin-bottom: 57px;
        }
        @media #{$mobile} {
            margin-bottom: 50px;
        }
    }
    &__pic-wrap {
        cursor: pointer;
        height: 230px;
        margin-bottom: 27px;
        img {
            display: block;
            max-width: 100%;
            max-height: 100%;
            margin: 0 auto;
        }
        @media #{$laptop} {
            height: 188px;
            margin-bottom: 21px;
        }
        @media #{$tablet} {
            height: 141px;
            margin-bottom: 14px;
        }
        @media #{$mobile} {
            height: 185px;
            margin-bottom: 12px;
        }
    }
    &__desc {
        color: $base-font-color;
        font-size: 18px;
        font-weight: 400;
        text-decoration: underline;
        cursor: pointer;
        &:hover {
            @media #{$tablet-plus} {
                text-decoration: none;
            }
        }
        @media #{$laptop} {
            font-size: 16px;
            line-height: 20px;
        }
        @media #{$tablet} {
            font-size: 12px;
            line-height: 15px;
        }
        @media #{$tablet} {
            margin: 0 auto;
            max-width: 291px;
            font-size: 15px;
            line-height: 20px;
        }
    }
}
.modal-vedeo {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 99;
    transform: translateY(-200%);
    display: block;
    &.shown {
        transform: translateY(0);
    }
    &__container {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: rgba(39, 39, 39, 0);
        transition: all .3s;
    }
    &.visible .modal-vedeo__container {
         background: rgba(39, 39, 39, .7);
    }
    &__dialog {
        transform: translate(0,-25%);
        transition: all .3s ease-out;
        opacity: 0;
        height: 100vh;
        max-height: 100vh;
        overflow-y: auto;
    }
    &.visible .modal-vedeo__dialog {
        transform: translate(0,0);
        opacity: 1;
    }
    &__content {
        background-color: #272727;
        padding: 20px 20px 28px;
        position: relative;
        left: 50%;
        width: calc(100% - 30px);
        max-width: 945px;
        margin: 30px 0;
        transform: translateX(-50%);
    }
    &__screen {
        position: relative;
        padding-bottom: 56.25%; /*     16:9 ( 4:3   75%) */
        padding-top: 30px;
        height: 0;
        overflow: hidden;
        margin-bottom: 15px;
        iframe {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }
    }
    &__caption {
        color: $base-font-color;
        font-size: 14px;
        font-weight: 400;
        line-height: 24px;
        margin-bottom: 7px;
    }
    &__description {
        color: #858585;
        font-size: 14px;
        font-weight: 400;
        line-height: 20px;
    }
    &__btn-wrap {
        overflow: hidden;
        padding-top: 14px;
        height: 50px;;
    }
    &__btn-close {
        width: 36px;
        height: 36px;
        border: 2px solid $base-font-color;
        background-color: rgba(171, 230, 71, 0);
        border-radius: 50%;
        float: right;
        position: relative;
        cursor: pointer;
        transition: all .3s;
        &:hover {
            @media #{$tablet-plus} {
                background-color: rgba(171, 230, 71, 1);
                border-color: rgba(171, 230, 71, 1);
                &::before,
                &::after {
                    background-color: $back-color;
                }
            }
        }
        &::before,
        &::after {
            content: '';
            display: block;
            width: 16px;
            height: 2px;
            background-color: $base-font-color;
            position: absolute;
            top: 15px;
            left: 8px;
            transition: all .3s;
        }
        &::before {
            transform: rotate(-45deg);
        }
        &::after {
            transform: rotate(45deg);
        }
    } 


}

// STYLES FOR RESAULTS PHOTO PAGE
.page-results-photo {
     .page-contenet {
         padding-bottom: 212px;
         @media #{$laptop} {
            padding-bottom: 89px;
         }
         @media #{$tablet} {
            padding-bottom: 102px;
         }
         @media #{$mobile} {
            padding-bottom: 50px;
         }
     }
     .selection-buttons {
        margin-bottom: 137px;
        @media #{$laptop} {
            margin-bottom: 48px;
         }
        @media #{$tablet} {
            margin-bottom: 43px;
         }
        @media #{$mobile} {
            margin-bottom: 23px;
         }
    }
}
.photo-list {
    &__item {
        @media #{$mobile} {
             &:nth-child(odd) .photo-list__pic-wrap {
                float: right;
             }
        }
    }
    &__pic-wrap {
        height: 230px;
        position: relative;
        cursor: pointer;
        margin-bottom: 30px;
        @media #{$tablet-plus} {
            &:hover .photo-list__pic-hover {
                opacity: 1;
            }
        }
        @media #{$laptop} {
            height: 187px;
            margin-bottom: 24px;
        }
        @media #{$tablet} {
            height: 141px;
            margin-bottom: 17px;
        }
        @media #{$mobile} {
            width: 130px;
            height: 83px;
            margin-bottom: 29px;
        }
    }
    &__pic,
    &__pic-hover {
        position: absolute;
        width: 100%;
        height: auto;
    }
    &__pic-hover {
        opacity: 0;
        transition: opacity .3s;
    }
}
.modal-image {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 99;
    transform: translateY(-200%);
    display: block;
    &.shown {
        transform: translateY(0);
    }
    &__container {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: rgba(39, 39, 39, 0);
        transition: all .3s;
    }
    &.visible .modal-image__container {
         background: rgba(39, 39, 39, .7);
    }
    &__dialog {
        transform: translate(0,-25%);
        transition: all .3s ease-out;
        opacity: 0;
        height: 100vh;
        max-height: 100vh;
        overflow-y: auto;
    }
    &.visible .modal-image__dialog {
        transform: translate(0,0);
        opacity: 1;
    }
    &__content {
        background-color: #272727;
        padding: 20px 20px 0;
        position: relative;
        left: 50%;
        width: calc(100% - 30px);
        max-width: 945px;
        margin: 30px 0;
        transform: translateX(-50%);
        @media #{$tablet} {
            max-width: 720px;
            padding-top: 23px;
            padding-bottom: 21px;
        }
        @media #{$mobile} {
            max-width: 320px;
            width: 100%;
            padding: 19px 15px 15px;
        }
    }
    &__screen {
        position: relative;
        overflow: hidden;
        margin-bottom: 17px;
        background-color: #000000;
        height: 530px;
        @media #{$tablet} {
            height: 400px;
            margin-bottom: 13px;
        }
        @media #{$mobile} {
            height: 170px;
            margin-bottom: 19px;
        }
        img {
            display: block;
            max-width: 100%;
            max-height: 100%;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            transition: opacity .3s;
            opacity: 0;
            &.active {
                opacity: 1;
            }
        }
    }
    &__description {
        color: #ffffff;
        font-size: 14px;
        font-weight: 400;
        line-height: 20px;
        max-width: calc(100% - 200px);
        @media #{$tablet} {
            font-size: 11px;
            line-height: 1.2;
        }
        @media #{$mobile} {
            max-width: calc(100% - 100px);
            font-size: 12px;
            line-height: 1.1;
        }
    }
    &__btn-wrap {
        height: 50px;
        @media #{$tablet} {
            height: 15px;
        }
    }
    &__btn-close,
    &__btn-prev,
    &__btn-next {
        width: 36px;
        height: 36px;
        border: 2px solid $base-font-color;
        background-color: rgba(171, 230, 71, 0);
        border-radius: 50%;
        float: right;
        position: relative;
        cursor: pointer;
        transition: all .3s;
        margin-top: -12px;
        &:hover {
            @media #{$tablet-plus} {
                background-color: rgba(171, 230, 71, 1);
                border-color: rgba(171, 230, 71, 1);
                &::before,
                &::after {
                    background-color: $back-color;
                }
            }
        }
        @media #{$tablet} {
            width: 27px;
            height: 27px;
            margin-top: -7px;
        }
        @media #{$mobile} {
            width: 22px;
            height: 22px;
            margin-top: -6px;
        }
    }
    &__btn-close {
        margin-left: 20px;
        @media #{$tablet} {
            margin-left: 13px;
        }
        &::before,
        &::after {
            content: '';
            display: block;
            width: 16px;
            height: 2px;
            background-color: $base-font-color;
            position: absolute;
            top: 15px;
            left: 8px;
            transition: all .3s;
            @media #{$tablet} {
                width: 11px;
                height: 1px;
                top: 11px;
                left: 7px;
            }
            @media #{$mobile} {
                width: 9px;
                top: 9px;
                left: 5px;
            }
        }
        &::before {
            transform: rotate(-45deg);
        }
        &::after {
            transform: rotate(45deg);
        }
    }
    &__btn-prev,
    &__btn-next {
        &::before,
        &::after {
            content: '';
            display: block;
            width: 9px;
            height: 2px;
            background-color: $base-font-color;
            position: absolute;
            transition: all .3s;
            @media #{$tablet} {
                width: 7px;
                height: 1px;
            }
            @media #{$mobile} {
                width: 6px;
            }
        }
    }
    &__btn-prev {
        &::before {
            transform: rotate(-45deg);
            top: 12px;
            left: 10px;
            @media #{$tablet} {
                top: 9px;
                left: 8px;
            }
            @media #{$mobile} {
                top: 7px;
                left: 6px;
            }
        }
        &::after {
            transform: rotate(45deg);
            top: 18px;
            left: 10px;
            @media #{$tablet} {
                top: 13px;
                left: 8px;
            }
            @media #{$mobile} {
                top: 11px;
                left: 6px;
            }
        }
    }
    &__btn-next {
        margin-left: 10px;
        @media #{$tablet} {
                margin-left: 7px;
        }
        &::before {
            transform: rotate(-45deg);
            top: 18px;
            left: 12px;
            @media #{$tablet} {
                top: 13px;
                left: 9px;
            }
            @media #{$mobile} {
                top: 11px;
                left: 7px;
            }
        }
        &::after {
            transform: rotate(45deg);
            top: 12px;
            left: 12px;
            @media #{$tablet} {
                top: 9px;
                left: 9px;
            }
            @media #{$mobile} {
                top: 7px;
                left: 7px;
            }
        }
    }

    &__pages {
        float: left;
        top: 5px;
        position: relative;
        @media #{$tablet} {
            top: 1px;
        }
        @media #{$mobile} {
            top: -1px;
        }
        div {
            float: left;
            color: #858585;
            font-size: 14px;
            font-weight: 400;
            line-height: 30px;
            @media #{$tablet} {
                font-size: 11px;
            }
            @media #{$mobile} {
                font-size: 10px;
            }
            &::before,
            &::after {
                display: none;
            }
        }
        &-txt {
            padding: 0 5px;
        }
    }
    


}

