// Exhibitors page styles
.exhibitors {
    margin-top: 39px;
    @media #{$laptop} {
        margin-top: 14px;
    }
    @media #{$tablet} {
        margin-top: 11px;
    }
    @media #{$mobile} {
        margin-top: 13px;
    }
    &__item-wrap {
        margin-bottom: 40px;
        @media #{$laptop} {
            margin-bottom: 36px;
        }
        @media #{$tablet} {
            margin-bottom: 30px;
        }
        @media #{$mobile} {
            margin-bottom: 26px;
            &:nth-child(odd) .exhibitors__item {
                float: right;
            }
        }
    }
    &__item {
        display: block;
        position: relative;
        width: 100%;
        height: 160px;
        background-color: $base-font-color;
        overflow: hidden;
        @media #{$laptop} {
            height: 130px;
        }
        @media #{$tablet} {
            height: 134px;
        }
        @media #{$mobile} {
            height: 79px;
            width: 130px;
        }
        img {
            display: block;
            max-width: 90%;
            max-height: 90%;
            position: relative;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
        }
        &::after {
            content: '';
            display: block;
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            background-color: #abe647;
            opacity: 0;
            transform: translateX(-100%);
            transition: all .3s;
        }
        @media #{$tablet-plus} {
            &:hover::after {
                transform: translateX(0);
                opacity: 0.3;
            }
        }
    }
}

// Exhibitors filter
.exhibitors-filter {
    padding: 30px 0;
    @media #{$laptop} {
        padding: 20px 0;
    }
    @media #{$mobile} {
        padding: 24px 0;
    }
    &__big-link {
        height: 210px;
        box-shadow: 0 0 0 rgba(220, 220, 220, 0);
        border-radius: 20px;
        background-color: transparent;
        display: block;
        width: 100%;
        transition: all .3s;
        text-decoration: none;
        &:hover {
            text-decoration: none;
            @media #{$tablet-plus} {
                box-shadow: 0 0 46px rgba(220, 220, 220, 0.35);
                background-color: $back-color;
            }        
        }
        @media #{$laptop} {
            height: 169px;
        }
        @media #{$tablet} {
            display: none;
        }
    }
    &__standart-link {
        display: none;
        width: 239px;
        margin: 0 15px 34px;
        text-decoration: none;
        height: 40px !important;
        &:hover,
        &:active,
        &:focus,
        &:visited {
            text-decoration: none;
        }
        &::after {
            text-align: center;
            font-size: 13px !important;
            font-weight: 700;
            text-transform: uppercase;
            letter-spacing: 0.65px !important;
            padding: 0 5px;
            text-decoration: none;
            height: 40px !important;
            line-height: 42px !important;
        }
        @media #{$tablet} {
            display: inline-block;
        }
        @media #{$mobile} {
            margin-bottom: 20px;
        }
    }
    &__standart-link.active {
        box-shadow: 0 12px 18px rgba(0, 0, 0, 0.2);
        border-radius: 20px;
        background-image: linear-gradient(to right, #bde557 0%, #7c9838 100%);
        cursor: default;
        &::after {
            color: #191919;
        }
    }
    &__big-link.active {
        box-shadow: 0 0 46px rgba(220, 220, 220, 0.35);
        background-color: $back-color;
        cursor: default;
    }
    &__pic-wrap {
        display: block;
        position: relative;
        padding: 20px 0 23px;
        margin: 0 auto;
        img {
            display: block;
            margin: 0 auto;
             @media #{$laptop} {
                height: 76px;
                width: auto;
                display: block;
                margin: 0 auto;
             }
        }
        @media #{$laptop} {
            padding: 20px 0 18px;
        }
    }
    &__title {
        color: $base-font-color;
        font-size: 18px;
        font-weight: 500;
        text-transform: uppercase;
        letter-spacing: 1.8px;
        display: block;
        text-align: center;
        padding: 0 5px 0 5px;
        @media #{$laptop} {
            font-size: 14px;
            letter-spacing: 1.74px;
        }
    }
    &__btn-wrap div:nth-child(odd) .exhibitors-filter__standart-link {
        float: right;
        @media  #{$mobile} {
            float: none;
        }
    }
    &__btn-container {
        @media  #{$mobile} {
            text-align: center;
        }
    }
}
