// STYLES FOR NOT NUMERICAL LIST
.not-numbered-list {
    margin-bottom: 4px;
    @media #{$tablet} {
        margin-bottom: 14px;
    }
    &__item {
        font-size: 18px;
        font-weight: 400;
        line-height: 24px;
        padding-bottom: 12px;
        @media #{$laptop} {
            font-size: 16px;
        }
        @media #{$tablet} {
            font-size: 14px;
            line-height: 21px;
            padding-bottom: 15px;
        }
        &::before {
            content: '';
            display: inline-block;
            width: 4px;
            height: 4px;
            background-color: $base-font-color;
            border-radius: 50%;
            margin-right: 30px;
            transform: translateY(-5px);
            @media #{$laptop} {
                width: 3px;
                height: 3px;
                transform: translate(5px, -5px);
                margin-right: 36px;
            }
            @media #{$tablet} {
                transform: translate(0px, -4px);
            }
        }
    }
}

// STYLES FOR NUMERICAL LIST
.numbered-list {
    counter-reset: myCounter;
    padding-left: 95px;
    margin-bottom: 33px;
    @media #{$laptop} {
        padding-left: 81px;
        margin-bottom: 56px;
    }
    @media #{$tablet} {
        margin-bottom: 18px;
    }
    @media #{$mobile} {
        padding-left: 0;
        margin-top: 38px;
        margin-bottom: 30px;
    }
    &__item {
        font-size: 18px;
        font-weight: 400;
        line-height: 23px;
        padding-bottom: 10px;
        position: relative;
        @media #{$laptop} {
            font-size: 16px;
        }
        @media #{$tablet} {
            font-size: 14px;
            line-height: 19px;
            padding-bottom: 16px;
        }
        @media #{$mobile} {
            line-height: 21px;
        }
        &::before {
            counter-increment: myCounter;
            content: counter(myCounter);
            display: inline-block;
            margin-right: 20px;
            border: 2px solid transparent;
            border-right-color: $base-font-color;
            border-radius: 50%;
            padding-right: 3px;
        }
    }
}
