.page-exhibition-zone {
    .page-contenet {
        padding-bottom: 118px;
        @media #{$laptop} {
            padding-bottom: 36px;
        }
        @media #{$tablet} {
            padding-bottom: 114px;
        }
        @media #{$mobile} {
            padding-bottom: 97px;
        }
    }
    .content-title {
        padding-bottom: 72px;
        @media #{$laptop} {
            padding-bottom: 67px;
        }
        @media #{$tablet} {
            padding-bottom: 30px;
        }
        @media #{$mobile} {
            padding-bottom: 37px;
        }
    }
}

.participants {
    &__title {
        color: $base-font-color;
        font-size: 24px;
        font-weight: 700;
        line-height: 24px;
        margin-bottom: 36px;
        @media #{$laptop} {
            font-size: 22px;
        }
        @media #{$tablet} {
            font-size: 24px;
            text-align: center;
        }
        @media #{$mobile} {
            font-size: 20px;
            margin-bottom: 27px;
        }
    }
    &__list {
        display: flex;
        flex-wrap: wrap;
        @media #{$mobile} {
            max-width: 290px;
            margin: 0 auto;
        }
        &-img-wrap {
            left: 8px;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            @media #{$laptop} {
                left: 4px;
                width: 47px;
            }
            @media #{$tablet} {
                width: 59px;
                top: -3px;
                transform: translateY(0);
                left: 5px;
            }
            @media #{$mobile} {
                width: 47px;
                top: 50%;
                left: 0px;
                transform: translateY(-50%);
            }
        }
        &-desc {
            color: $base-font-color;
            font-size: 18px;
            font-weight: 400;
            line-height: 24px;
            padding-left: 97px;
            @media #{$laptop} {
                font-size: 16px;
                line-height: 21px;
                padding-left: 82px;
            }
            @media #{$tablet} {
                font-size: 18px;
                line-height: 24px;
                padding-left: 95px;
            }
            @media #{$mobile} {
                font-size: 14px;
                line-height: 21px;
                padding-left: 76px;
            }
        }
    }
    &__item {
        position: relative;
        margin-bottom: 48px;
        @media #{$laptop} {
            margin-bottom: 42px;
        }
        @media #{$tablet} {
            width: 50%;
            margin-bottom: 48px;
            &:nth-child(odd){
                padding-right: 30px;
                width: 345px;
            }
        }
        @media #{$mobile} {
            width: 100%;
            margin-bottom: 48px;
            margin-bottom: 42px;
            &:nth-child(odd){
                padding-right: 0;
                width: 100%;
            }
        }
    }
}

.exhibition-zone {
    &__description {
        text-align: center;
        box-shadow: 0 0 24px rgba(94, 94, 94, 0.4);
        padding: 110px 86px 127px;
        background: url('../images/VI.png') center -78px no-repeat;
        background-size: 110%;
        @media #{$laptop} {
            padding: 95px 55px 90px;
            background: url('../images/VI.png') center -68px no-repeat;
            background-size: 117%;
        }
        @media #{$tablet} {
            width: 437px;
            margin: 56px auto 0;
            padding: 92px 46px 90px;
            transform: translateX(-20px);
            background: url('../images/VI.png') center -70px no-repeat;
            background-size: 122%;
        }
        @media #{$mobile} {
            max-width: 290px;
            transform: translateX(-2px);
            margin: 45px auto 0;
            padding: 42px 30px 51px;
            background: url('../images/VI.png') -36px -58px no-repeat;
            background-size: 156%;
        }
        &-title {
            color: $accent-color;
            font-size: 24px;
            font-weight: 700;
            line-height: 24px;
            text-transform: uppercase;
            margin-bottom: 26px;
            @media #{$laptop} {
                font-size: 22px;
                margin-bottom: 24px;
            }
        }
        &-content {
            color: $base-font-color;
            font-weight: 400;
            line-height: 26px;
            font-size: 20px;
            @media #{$laptop} {
                font-size: 18px;
            }
            @media #{$mobile} {
                font-size: 16px;
                line-height: 21px;
            }
        }
    }
    &__places {
        margin-top: 123px;
        margin-bottom: 64px;
        @media #{$laptop} {
            margin-top: 126px;
            margin-bottom: 33px;
        }
        @media #{$tablet} {
            margin-top: 110px;
            margin-bottom: 82px;
        }
        @media #{$mobile} {
            margin-top: 82px;
            margin-bottom: 56px;
        }
    }
    &__btn-wrap {
        .btn-default::after {
            padding: 0 59px;
            @media #{$laptop} {
                padding: 0 31px;
            }
        }
    }
}
