.page-become-member {
    .content-title {
        font-size: 50px;
        font-weight: 700;
        line-height: 24px;
        letter-spacing: 6.7px;
        padding: 67px 0 83px;
        @media #{$laptop} {
            font-size: 36px;
            line-height: 24px;
            letter-spacing: 4.1px;
            padding-bottom: 79px;
        }
        @media #{$tablet} {
            font-size: 30px;
            letter-spacing: 3.2px;
            padding-bottom: 57px;
            padding-top: 88px;
        }
        @media #{$mobile} {
            font-size: 26px;
            letter-spacing: 3px;
            padding-top: 78px;
            padding-bottom: 60px;
        }
    }
    .page-contenet {
        padding-bottom: 149px;
        @media #{$laptop} {
            padding-bottom: 120px;
        }
        @media #{$tablet} {
            padding-bottom: 111px;
        }
        @media #{$mobile} {
            padding-bottom: 92px;
        }
    }
}

.become-member__btn-wrap {
    text-align: center;
    margin-top: 27px;
    @media #{$tablet} {
        margin-top: 22px;
    }
    @media #{$mobile} {
        margin-top: 48px;
    }
    .btn-default {
        height: 44px;
        border-radius: 999px;
        @media #{$mobile} {
            width: 100%;
            max-width: 290px;
        }
        &::after {
            font-size: 18px;
            font-weight: 700;
            text-transform: uppercase;
            line-height: 46px;
            padding: 0 78px;
            @media #{$laptop} {
                font-size: 16px;
                padding: 0 85px;
            }
        }
    }
}

