// Styles for forms elements
/* placeholder styles for input and textarea */
.form-group input::placeholder {color: $form-control-font; opacity: 1; transition: opacity 0.3s ease; font-size: inherit;}
.form-group input:-ms-input-placeholder {color: $form-control-font; opacity: 1; transition: opacity 0.3s ease; font-size: inherit;}
.form-group input::-ms-input-placeholder {color: $form-control-font; opacity: 1; transition: opacity 0.3s ease; font-size: inherit;}
.form-group textarea::placeholder {color: $form-control-font; opacity: 1; transition: opacity 0.3s ease; font-size: inherit;}
.form-group textarea:-ms-input-placeholder {color: $form-control-font; opacity: 1; transition: opacity 0.3s ease; font-size: inherit;}
.form-group textarea::-ms-input-placeholder {color: $form-control-font; opacity: 1; transition: opacity 0.3s ease; font-size: inherit;}
/* Smooth disappearance of placeholder */
.form-group input:focus::placeholder {opacity: 0; transition: opacity 0.3s ease;}
.form-group input:focus:-ms-input-placeholder {opacity: 0; transition: opacity 0.3s ease;}
.form-group input:focus::-ms-input-placeholder {opacity: 0; transition: opacity 0.3s ease;}
.form-group textarea:focus::placeholder {opacity: 0; transition: opacity 0.3s ease;}
.form-group textarea:focus:-ms-input-placeholder {opacity: 0; transition: opacity 0.3s ease;}
.form-group textarea:focus::-ms-input-placeholder {opacity: 0; transition: opacity 0.3s ease;}

.form-group .form-control {
    display: block;
    width: 100%;
    height: 44px !important;
    padding: 0 13px;
    font: 400 16px/42px 'Roboto', sans-serif;
    color: $form-control-font;
    text-align: left;
    background-color: $base-font-color;
    background-image: none;
    border: 1px solid #bababa;
    border-radius: 999px;
    box-shadow: none !important;
    transition: all .3s;
}
/* Error form-control state */
.form-group.has-error .form-control {
    border-color: $form-control-error;
    border-width: 2px;
    line-height: 40px;
}
/* Valid input state */
.form-group.has-success .form-control {
  border-color: $form-control-valid;
}

/* Default styles for message in help bclocks in forms */
%help-block {
    display: block;
    margin-top: 5px;
    margin-bottom: 0;
    padding: 0 17px;
    font: 400 12px/1.2 'Roboto', sans-serif;
    text-align: left;
}
.help-block {
    min-height: 10px;
}
.has-error .help-block {
    @extend %help-block;
    color: $help-block-error;
}
.has-success .help-block {
    @extend %help-block;
    color: $form-control-valid;
}
/* Default state for textarea in forms */
.form-group textarea.form-control {
    resize: none;
    min-height: 86px;
    line-height: 1.2;
    border-radius: 17px;
    padding: 7px 13px;
}
_:-ms-fullscreen, :root .form-group .form-control[type="text"] {
    line-height: 32px;
}

/* Default state for checkbox and radio */
.checkbox label,
.radio label {
    font: 400 14px/1.2 'Roboto', sans-serif;
    color: $base-font-color;
    min-height: 20px;
    padding-left: 25px;
    margin-bottom: 0;
    cursor: pointer;
    line-height: 1.3;
    @media #{$mobile} {
        font-size: 12px;
        letter-spacing: 0.3px;
    }
}
.has-error.checkbox label,
.has-success.checkbox label,
.has-error .radio label,
.has-success .radio label {
  color: $base-font-color;
}

/* Default anchor in label */
.checkbox label a,
.radio label a {
    color: $base-font-color;
    text-decoration: underline;
}
.checkbox label a:hover,
.checkbox label a:active,
.checkbox label a:visited,
.checkbox label a:focus,
.radio label a:hover,
.radio label a:active,
.radio label a:visited,
.radio label a:focus {
    text-decoration: none;
}

/* Hide checkbox and radio */
.checkbox input[type="checkbox"],
.radio input[type="radio"] {
    border: 0 !important;
    clip: rect(1px, 1px, 1px, 1px) !important;
    -webkit-clip-path: inset(50%) !important;
    clip-path: inset(50%) !important;
    height: 1px !important;
    overflow: hidden !important;
    padding: 0 !important;
    position: absolute !important;
    width: 1px !important;
    white-space: nowrap !important;
}

.checkbox input[type="checkbox"] + .controller::before {
    content: '';
    display: block;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    border: 1px solid #bababa;
    position: absolute;
    top: 1px;
    left: 0;
    background-color: $base-font-color;
}
.checkbox input[type="checkbox"] + .controller::after {
    content: '';
    display: block;
    width: 9px;
    height: 9px;
    border-radius: 50%;
    background-color: #bababa;
    opacity: 0;
    transition: all .3s;
    position: absolute;
    top: 4px;
    left: 3px;
}
.checkbox input[type="checkbox"]:checked + .controller::after {
    background-color: $form-control-valid !important;
    opacity: 1 !important;
}
.checkbox.has-error input[type="checkbox"] + .controller::after {
    background-color: $form-control-error;
    opacity: 1;
}
.checkbox input[type="checkbox"]:hover + .controller::after {
    opacity: 1;
}
.checkbox.has-error .help-block {
    margin-top: 2px;
    padding: 0 15px 0 25px;
}
form .checkbox {
    max-width: 520px;
    margin: 0 auto;
    position: relative;
}
form .subscribe__btn-wrap {
    text-align: center;
    margin: 34px 0 0;
    @media #{$laptop} {
        margin-top: 24px;
    }
    @media #{$mobile} {
        margin-top: 33px;
    }
}

.big-form .form-group .form-control {
    font-size: 16px;
    font-weight: 700;
    padding: 0 20px;
}

.radio-group {
    margin-bottom: -9px;
    @media #{$tablet} {
        margin-bottom: -6px;
    }
    @media #{$mobile} {
        margin-top: 20px;
        margin-bottom: 20px;
    }
    &__content {
        display: flex;
        align-items: center;
        margin-top: -18px;
        @media #{$mobile} {
            max-width: 290px;
            margin: 0 auto;
            flex-direction: column;
            align-items: flex-start;
        }
    }
    &__title {
        color: $base-font-color;
        font-size: 16px;
        font-weight: 700;
        padding-left: 7px;
        @media #{$laptop} {
            padding-left: 0;
        }
    }
    &__buttons {
        padding-left: 23px;
        @media #{$laptop} {
            padding-left: 50px;
        }
        @media #{$tablet} {
            padding-left: 14px;
        }
        @media #{$mobile} {
            padding-left: 0;
        }
    }
    .radio {
        margin-top: 12px;
        margin-bottom: 12px;
        label {
            padding-left: 30px;
            @media #{$mobile} {
                font-size: 14px;
                padding-left: 51px;
            }
        }
    }
}


.big-form .checkbox input[type="checkbox"] + .controller::before,
.big-form .radio input[type="radio"] + .controller::before {
    content: '';
    display: block;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    border: 2px solid #e7e7e7;
    position: absolute;
    top: -1px;
    left: 0;
    background-color: transparent;
}
.big-form .checkbox input[type="checkbox"] + .controller::after,
.big-form .radio input[type="radio"] + .controller::after {
    content: '';
    display: block;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: #bababa;
    opacity: 0;
    transition: all .3s;
    position: absolute;
    top: 5px;
    left: 6px;
}
.big-form .checkbox input[type="checkbox"]:checked + .controller::after,
.big-form .radio input[type="radio"]:checked + .controller::after {
    background-color: $accent-color !important;
    opacity: 1 !important;
}
.big-form .checkbox input[type="checkbox"]:hover + .controller::after,
.big-form .radio input[type="radio"]:hover + .controller::after {
    opacity: 1;
}

.big-form .checkbox {
    max-width: none;
    margin: 0;
    @media #{$mobile} {
        max-width: 290px;
    margin: 0 auto;
    }
    label {
        padding: 0;
        @media #{$laptop} {
            padding-left: 50px;
        }
        @media #{$tablet} {
            padding-left: 33px;
            line-height: 1.5;
            padding-right: 20px;
        }
        @media #{$mobile} {
            font-size: 14px;
            padding-left: 51px;
            line-height: 1.4;
        }
    }
    .controller {
        position: absolute;
        top: 6px;
        left: -54px;
        @media #{$laptop} {
            left: 0;
        }
    }
}
.big-form {
    &__items .form-group {
        @media #{$mobile} {
            max-width: 290px;
            margin-left: auto;
            margin-right: auto;
        }
    }
    .has-error .help-block, 
    .has-success .help-block {
        padding: 0 22px;
    }
    .radio-group.has-error .help-block, 
    .radio-group.has-success .help-block {
        padding: 0 0 0 143px;
        margin-top: -3px;
        @media #{$laptop} {
            padding-left: 165px;
        }
        @media #{$tablet} {
            padding-left: 127px;
        }
        @media #{$mobile} {
            max-width: 290px;
            margin: 0 auto;
            padding-left: 51px;
            margin-top: -5px;
        }
    }
    .checkbox.has-error .help-block, 
    .checkbox.has-success .help-block {
        padding: 0;
        @media #{$laptop} {
            padding-left: 50px;
            margin-top: 2px;
        }
        @media #{$tablet} {
            padding-left: 33px;
        }
        @media #{$mobile} {
            padding-left: 51px;
            margin-top: 4px;
        }
    }

    &__textarea {
        .form-group {
            @media #{$mobile} {
                max-width: 290px;
                margin-left: auto;
                margin-right: auto;
            }
            textarea.form-control {
                min-height: 100px;
                border-radius: 10px;
                padding: 12px 20px;
                margin-bottom: 20px;
                @media #{$laptop} {
                    margin-bottom: 17px;
                }
                 @media #{$mobile} {
                        min-height: 187px;
                        margin-bottom: 11px;
                }
            }
        }
    }
}




